/* #region Stars */
.stars {
  display: flex;

  &__star {
    display: flex;
    height: 16px;
    width: 16px;
    margin-right: 4px;
    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
  }

  @for $i from 1 through 5 {
    &--#{$i} &__star:nth-child(-n + #{$i} ) {
      background-image: url(../images/star-active.svg);
    }
  }
}
/* #endregion */
